import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../components/login.vue'
import register from '../components/register.vue'
import home from '../components/home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: 'login'
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/register',
    name: 'register',
    component: register
  },
  {
    path: '/home',
    name: 'home',
    component: home
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if (to.path == '/register') {
    next()
    return
  }
  if (to.path == '/login') {
    next()
    return
  }

  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr && to.path != '/register') {
    next('/login')
  } else {
    next()
  }
})


export default router
